.about {
  background-color: $second-background-color;

  &__label {
    margin-bottom: 15px;

    font-family: 'Titan One';
    font-weight: 400;
    font-size: 18px;

    @include tablet {
      margin-bottom: 16px;
      font-size: 26px;
      line-height: calc(30 / 26);
    }

    @include desktop {
      font-size: 30px;
      letter-spacing: 0.04em;
      line-height: calc(34 / 30);
    }

    line-height: calc(21 / 18);
    text-align: center;
    text-transform: uppercase;
    color: $second-title-color;
  }

  &__title {
    margin-bottom: 32px;

    @include tablet {
      margin-bottom: 40px;
      font-size: 48px;
      line-height: calc(55 / 48);
    }

    @include desktop {
      margin-bottom: 60px;
      font-size: 58px;
      line-height: calc(66 / 58);
      letter-spacing: 0.06em;
    }

    font-family: 'Titan One';
    font-weight: 400;
    font-size: 34px;

    line-height: calc(39 / 34);
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $first-title-color;
  }

  @media screen and (max-width: 767px) {
    padding-bottom: 40px;
  }

  @include desktop {
    background-image: url(../images/about/about-desk-2-1x.webp);
    @include retina {
      background-image: url(../images/about/about-desk-2-2x.webp);
    }

    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: 235px;
  }

  .tab-direction {
    position: relative;

    @include tablet {
      display: flex;
    }

    &__img {
      width: 100%;
    }

    &__picture {
      display: block;
      @media screen and (max-width: 767px) {
        margin-bottom: 40px;
      }

      @include tablet {
        width: 336px;
        margin-right: 32px;
      }

      @include desktop {
        width: 500px;
        margin-right: 100px;
      }

      @media screen and (max-width: 767px) {
        margin-bottom: 40px;
      }
    }
  }

  .about-description {
    @include tablet {
      width: 334px;
    }

    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    @media screen and (max-width: 767px) {
      background-image: url(../images/about/about-2-1x.webp);
      @include retina {
        background-image: url(../images/about/about-2-2x.webp);
      }
    }
    @include desktop {
      width: 463px;
      padding-top: 90px;
      background-size: cover;
    }

    &__pre-text {
      font-weight: 500;
      font-size: 12px;
      line-height: calc(19 / 12);
      color: $first-text-color;

      margin-bottom: 40px;

      @include tablet {
        font-size: 14px;
        line-height: calc(25 / 14);
      }

      @include desktop {
        margin-bottom: 32px;

        font-size: 16px;
        line-height: calc(28 / 16);
        letter-spacing: 0.04em;
      }
    }

    &__post-text {
      font-weight: 500;
      font-size: 12px;
      line-height: calc(19 / 16);
      color: #907e82;

      margin-bottom: 20px;

      @include tablet {
        margin-bottom: 40px;
        font-size: 14px;
        line-height: calc(19 / 12);
        letter-spacing: 0.04em;
      }

      @include desktop {
        margin-bottom: 40px;

        font-size: 14px;
        line-height: calc(27 / 14);
        letter-spacing: 0.04em;
      }
    }

    &__span {
      display: block;
      margin-top: 20px;
    }

    &__button {
      display: flex;
      align-items: center;
      justify-content: center;

      min-width: 177px;
      height: 44px;

      font-weight: 700;
      font-size: 16px;
      line-height: calc(21 / 16);

      color: $first-title-color;
      border-radius: 22px;
      background-color: $second-text-color;

      border: none;
      padding: 0;
      cursor: pointer;
      transition: 500ms background-color cubic-bezier(0.165, 0.84, 0.44, 1),
        500ms color cubic-bezier(0.165, 0.84, 0.44, 1),
        transform 500ms cubic-bezier(0.165, 0.84, 0.44, 1),
        box-shadow 500ms cubic-bezier(0.165, 0.84, 0.44, 1);

      &:hover,
      &:focus {
        color: $second-text-color;
        background-color: $first-title-color;

        box-shadow: -3px -4px 5px #7b0c2847;
      }
    }

    &__arrow {
      margin-left: 13px;
      fill: $first-title-color;
      transition: 500ms fill cubic-bezier(0.165, 0.84, 0.44, 1);

      .about-description__button:hover &,
      .about-description__button:focus & {
        fill: $second-text-color;
      }
    }
  }

  .about-modal {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;

    transition: transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms;

    &__backdrop {
      border-radius: 10px;
      display: flex;
      flex-wrap: wrap;
      align-content: space-between;
      justify-content: center;

      @media screen and (min-width: 320px) and (max-width: 380px) {
        padding: 20px;
      }

      padding: 60px;

      width: 100%;
      height: 100%;

      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      background-image: url(../images/about/milk.webp);
      background-color: #ffd4d9;

      overflow: auto;
    }

    &.is-hidden {
      transform: translate(-50%, -100%);
      opacity: 0;
      pointer-events: none;
      visibility: hidden;
      transition: 500ms;
    }

    &__text {
      font-weight: 500;
      font-size: 12px;
      line-height: calc(25 / 14);
      color: #847679;

      @include tablet {
        font-size: 14px;
        line-height: calc(19 / 12);
        letter-spacing: 0.04em;
      }

      @include desktop {
        font-size: 16px;
        line-height: calc(27 / 14);
        letter-spacing: 0.04em;
      }
    }

    &__button {
      display: flex;
      align-items: center;
      justify-content: center;

      min-width: 137px;
      height: 44px;

      font-weight: 700;
      font-size: 16px;
      line-height: calc(21 / 16);

      color: $second-text-color;
      border-radius: 22px;
      background-color: $first-title-color;

      border: none;
      padding: 0;
      cursor: pointer;
      transition: 500ms background-color cubic-bezier(0.165, 0.84, 0.44, 1),
        500ms color cubic-bezier(0.165, 0.84, 0.44, 1),
        transform 500ms cubic-bezier(0.165, 0.84, 0.44, 1),
        box-shadow 500ms cubic-bezier(0.165, 0.84, 0.44, 1);

      &:hover,
      &:focus {
        color: $first-title-color;
        background-color: $second-text-color;
        transform: scale(1.1);
        box-shadow: -3px -4px 5px #7b0c2847;
      }
    }

    &__arrow {
      transform: rotate(180deg);
      margin-right: 5px;
      fill: $second-text-color;
      transition: 500ms fill cubic-bezier(0.165, 0.84, 0.44, 1);

      .about-modal__button:hover &,
      .about-modal__button:focus & {
        fill: $first-title-color;
      }
    }
  }

  .about-cont {
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      padding: 0 32px;
    }

    @include desktop {
      padding: 0 16px;
    }
  }
}
