.btn {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  // letter-spacing: 0.06em;
  color: #d41443;
  background-color: #ffffff;
  border-radius: 22px;
  min-width: 210px;
  height: 44px;
  border: none;
  cursor: pointer;

  transition: 500ms background-color cubic-bezier(0.165, 0.84, 0.44, 1),
    500ms color cubic-bezier(0.165, 0.84, 0.44, 1);

  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}
